/*Footer.css*/

footer {
	width: 100%;
	display: flex;
	height: 4em;
	bottom: 0;
	left: 0;
	font-family: 'Montserrat';
	font-weight: 300;
	font-size: 14px;
	grid-column: 1 / 3;
}

footer>span {
	margin: auto;
}

footer.box-shadow {
	box-shadow: 0 -26px 15px -27px var(--smoke-lt);
}